.App{
	height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.App-logo{
	width: 10rem;
}

.flyout {
	display:flex;
	flex-direction: column;
	min-height:100vh;
	justify-content: space-between;
}

.home-feature-box .fa {
	font-size:6rem;
}

.home-feature-box span {
	display: block;
	color:#111;
	font-weight:bold;
	margin-top:1.5rem;
}

.example-components-list li > a {
  color: #495057;
}

.example-components-list li:last-child > a {
	border-bottom:0;
}

.example-components-list li > a .fa {
    color: rgba(0, 0, 0, .35);
    float: right;
}

/**
 * SECTION INTRO CSS START
*/
#apppage .gradient {
  background: -moz-linear-gradient(45deg, #388e3c, rgba(29, 210, 177, 0.7) 100%);
  background: -webkit-linear-gradient(45deg, #388e3c, rgba(29, 210, 177, 0.7) 100%);
  background: -webkit-gradient(linear, 45deg, from(#388e3c), to(rgba(29, 210, 177, 0.7)));
  background: -o-linear-gradient(45deg, #388e3c, rgba(29, 210, 177, 0.7) 100%);
  background: linear-gradient(45deg, #388e3c, rgba(29, 210, 177, 0.7) 100%);
}

#apppage .view {
  background-image: url('https://mdbootstrap.com/img/Photos/Others/architecture.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  height: 90vh;
}

#apppage h6 {
  line-height: 1.7;
}

#apppage .navbar {
  transition: background .5s ease-in-out,padding .5s ease-in-out;
}

#apppage .top-nav-collapse {
  background: #424f95 !important;
}

.font-weight-bold {
    font-weight: 950!important;
}

@media (min-width: 576px){
.h1-responsive {
    font-size: 200% !important;
}
}

@media (max-width: 768px) {
  #apppage .navbar:not(.top-nav-collapse) {
      background: #424f95 !important;
  }
  .h1-responsive {
    font-size: 200% !important;
}
}

@media (min-width: 1200px){
.mt-xl-5, .my-xl-5 {
    margin-top: 3rem!important;
    margin-right: auto;
}
}

.h1-responsive {
    font-size: 250%;
}

